
.slider {
  position: relative;
  margin: 0 auto;
  width: 80%;
  height: 185px;
  overflow: hidden;
}
.slider .dots {
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.slider .dots>span {
  display: inline-block;
  width: 20px;
  height: 3px;
  background-color: #fff;
  margin-right: 3px;
  cursor: pointer;
}
.slider .dots>span.active {
  background-color: #f00;
}
.slider #arr #right,
.slider #arr #left {
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 30px;
  color: #fff;
  background: #000;
  cursor: pointer;
  text-align: center;
  font-weight: bold;
  font-family: '黑体';
  opacity: 0.3;
  border: 1px solid #fff;
}
.slider #arr #right {
  right: 0;
  left: auto;
}
.slider .box {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.slider .box>li {
  float: left;
  height: 160px;
  position: relative;
}
.slider .box>li .mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(7,17,27,0.5);
}
.slider .box>li>img {
  width: 100%;
  height: 100%;
}
.slider .box>li.active {
  height: 185px;
  transition: all 0.5s;
}
.slider .box>li.active .mask {
  display: none;
}


