
.controlList {
  position: fixed;
  bottom: 50px;
  right: 0;
  width: 475px;
  height: 430px;
  background-color: #fff;
  z-index: 999;
  font-size: 12px;
  text-align: left;
  border: 1px solid rgba(7,17,27,0.1);
  overflow: auto;
}
.controlList .head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  border-bottom: 1px solid rgba(7,17,27,0.2);
}
.controlList .head>div {
  cursor: default;
}
.controlList .head .head-one {
  -webkit-box-flex: 3;
  -ms-flex: 3;
      flex: 3;
  box-flex: 3;
  min-width: 0;
  padding-left: 30px;
}
.controlList .head .head-two {
  -webkit-box-flex: 2;
  -ms-flex: 2;
      flex: 2;
  box-flex: 2;
  min-width: 0;
}
.controlList .head .head-three {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
}
.controlList>li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 45px;
  line-height: 45px;
  padding: 0 30px;
}
.controlList>li .songName {
  -webkit-box-flex: 3;
  -ms-flex: 3;
      flex: 3;
  box-flex: 3;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.controlList>li .songSinger {
  -webkit-box-flex: 2;
  -ms-flex: 2;
      flex: 2;
  box-flex: 2;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.controlList>li .deleteSong {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
  color: #b7b7b8;
}
.controlList>li:hover {
  background-color: #e8e8eb;
}
.controlList>li.active {
  position: relative;
}
.controlList>li.active .songName,
.controlList>li.active .deleteSong {
  color: #df3b3b;
}
.controlList>li.active .songSinger>span>a {
  color: #df3b3b;
}
.controlList>li.active:before {
  width: 3px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  display: block;
  content: "";
  background-color: #df3b3b;
}


