
.header {
  box-sizing: border-box;
  font-size: 14px;
  color: #fff;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
  padding: 0 20px;
  background-color: #bc2f2e;
  cursor: default;
}
.header .left {
  float: left;
}
.header .left>i {
  cursor: pointer;
}
.header .left>h2 {
  display: inline-block;
  margin-left: 30px;
}
.header .right {
  float: right;
}
.header .right>i {
  margin-left: 30px;
}


