
.songs {
  font-size: 12px;
  color: #b0b0b1;
  font-size: 12px;
  text-align: left;
}
.songs .song {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 45px;
  line-height: 45px;
}
.songs .song .name-section,
.songs .song .artist-section {
  -webkit-box-flex: 2;
  -ms-flex: 2;
      flex: 2;
  box-flex: 2;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.songs .song .name-section>i,
.songs .song .artist-section>i {
  margin: 0 12px;
}
.songs .song .name-section .no,
.songs .song .artist-section .no,
.songs .song .name-section .songName,
.songs .song .artist-section .songName {
  color: #333;
}
.songs .song .artist-section,
.songs .song .songAlbum {
  -webkit-box-flex: 2;
  -ms-flex: 2;
      flex: 2;
  box-flex: 2;
  min-width: 0;
}
.songs .song .duration {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
}
.songs .song:neh-child(odd) {
  background-color: #f4f4f6;
}


