
.favo {
  padding: 20px 30px;
  font-size: 12px;
  text-align: left;
  position: relative;
}
.favo .list .info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
}
.favo .list .info .cover {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 200px;
          flex: 0 0 200px;
  width: 200px;
  height: 200px;
  margin-right: 20px;
  position: relative;
}
.favo .list .info .cover>img {
  width: 100%;
  height: 100%;
}
.favo .list .info .others {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.favo .list .info .others .title {
  font-size: 20px;
  margin-bottom: 12px;
}
.favo .list .info .others .user>div {
  display: inline-block;
  margin-right: 12px;
  margin-bottom: 20px;
}
.favo .list .info .others .user .avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
}
.favo .list .info .others .user .avatar>img {
  width: 100%;
  height: 100%;
}
.favo .list .info .others .user .userName,
.favo .list .info .others .user .time {
  font-size: 13px;
  line-height: 30px;
  vertical-align: top;
}
.favo .list .info .others .user .userName {
  color: #666;
}
.favo .list .info .others .user .time {
  color: #aeaeaf;
}
.favo .list .info .btns {
  margin-bottom: 20px;
}
.favo .list .info .btns>a {
  display: inline-block;
  height: 18px;
  line-height: 18px;
  padding: 8px;
  color: #000;
  letter-spacing: 1px;
  border-radius: 5px;
  border: 1px solid #c3c4c8;
  cursor: pointer;
}
.favo .list .info .tag {
  color: #000;
  margin-bottom: 15px;
}
.favo .list .info .tag>a {
  box-sinzing: border-box;
  display: inline-block;
  margin: 0px 10px 3px 0px;
  padding: 3px;
  height: 18px;
  font-size: 12px;
  line-height: 18px;
  border: 1px solid #c3c4c8;
  border-radius: 7px;
  cursor: pointer;
}
.favo .list .info .des>p {
  margin-top: 4px;
  line-height: 18px;
  color: #666;
  font-weight: 600;
  max-height: 35px;
  overflow: hidden;
  cursor: pointer;
}
.favo .list .info .des>span {
  color: #000;
}
.favo .list .playAll {
  font-size: 15px;
  margin-left: 12px;
  margin-bottom: 15px;
  cursor: pointer;
}
.favo .bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 1;
  -webkit-filter: blur(30px);
          filter: blur(30px);
  opacity: 0.2;
}


