
.toplist {
  padding: 20px 5%;
  text-align: left;
}
.toplist .content .special {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: left;
  color: #818181;
}
.toplist .content .special>li {
  width: 48%;
  height: 120px;
  border: 1px solid rgba(7,17,27,0.2);
  margin-bottom: 30px;
}
.toplist .content .special>li>a {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  cursor: default;
}
.toplist .content .special>li>a .cover {
  width: 120px;
  height: 120px;
}
.toplist .content .special>li>a .top3 {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
  width: 1px;
  height: 100%;
  padding-top: 30px;
  padding-left: 10px;
}
.toplist .content .special>li>a .top3>li {
  height: 20px;
  line-height: 20px;
  margin-bottom: 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.toplist .content .special>li>a .top3>li .songArtists>span>a {
  color: #818181;
}
.toplist .content .global {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-size: 15px;
  color: #000;
}
.toplist .content .global>li {
  width: 150px;
  height: 180px;
  margin-right: 20px;
  margin-bottom: 30px;
  overflow: hidden;
}
.toplist .content .global>li>a {
  cursor: default;
  display: block;
  height: 100%;
}
.toplist .content .global>li>a .cover {
  width: 100%;
  height: 150px;
  margin-bottom: 10px;
}
.toplist .content .global>li>a .listName {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


