
.slideMenu {
  width: 15%;
  overflow: auto;
  text-align: left;
  font-size: 14px;
  color: #bfbfc0;
  background-color: #f3f3f5;
}
.slideMenu>i {
  height: 45px;
  line-height: 45px;
  margin-left: 10px;
  font-size: 15px;
  cursor: pointer;
}
.slideMenu .content>h5 {
  margin-left: 10px;
  height: 45px;
  line-height: 45px;
}
.slideMenu .content .links>li {
  box-sizing: border-box;
  height: 40px;
  padding-left: 12px;
}
.slideMenu .content .links>li>a {
  display: block;
  font-size: 14px;
  color: #000;
  height: 40px;
  line-height: 40px;
}
.slideMenu .content .links>li>a>i {
  display: inline-block;
  margin-right: 15px;
  font-size: 15px;
}
.slideMenu .content .links>li>a.active {
  color: #df3b3b;
}
.slideMenu .content .links>li:hover {
  background-color: #e8e8eb;
}
.slideMenu .content .collectList>li {
  position: relative;
}
.slideMenu .content .collectList>li .icon-cross {
  position: absolute;
  top: 3px;
  right: 14px;
  display: none;
  font-size: 12px;
}
.slideMenu .content .collectList>li>a>span {
  width: 70%;
  height: 40px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
  color: #000;
}
.slideMenu .content .collectList>li>a>img {
  width: 35px;
  height: 35px;
  margin-right: 5px;
}
.slideMenu .content .collectList>li>a.active {
  color: #000;
}
.slideMenu .content .collectList>li:hover .icon-cross {
  display: inline;
  color: #999;
}


