
.loading {
  margin: 0 auto;
  margin-top: 50px;
  width: 30px;
  height: 30px;
  background-color: #fafafc;
  -webkit-animation: roll 4s linear infinite;
          animation: roll 4s linear infinite;
  position: relative;
}
.loading .icon-spinner10 {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 30px;
  color: #df3b3b;
}


