
.searchArtist .artists .artist {
  height: 70px;
  text-align: left;
  overflow: hidden;
}
.searchArtist .artists .artist>a {
  display: block;
  height: 70px;
  padding: 10px;
  box-sizing: border-box;
}
.searchArtist .artists .artist>a .cover {
  width: 50px;
  height: 50px;
  margin-right: 5px;
}
.searchArtist .artists .artist>a .name {
  display: inline-block;
  height: 50px;
  line-height: 50px;
  vertical-align: top;
  color: #5584b2;
  font-size: 17px;
}
.searchArtist .artists .artist:nth-child(odd) {
  background-color: #f4f4f6;
}


