
.search {
  position: relative;
  padding: 20px 25px;
}
.search .searchBar {
  width: 100%;
  height: 45px;
  color: #ccc;
  position: relative;
}
.search .searchBar>input {
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  width: 95%;
  height: 45px;
  font-size: 15px;
  padding: 5px;
  border: 1px solid #ccc;
  border-right: none;
}
.search .searchBar>input:focus {
  color: #000;
}
.search .searchBar>i {
  position: absolute;
  right: 0;
  top: 0;
  width: 5%;
  box-sizing: border-box;
  display: inline-block;
  height: 45px;
  line-height: 45px;
  border: 1px solid #ccc;
  border-left: none;
  font-size: 18px;
}
.search .nav {
  position: absolute;
  left: 30px;
  top: 90px;
  width: 95%;
  overflow: hidden;
  border-bottom: 1px solid rgba(7,17,27,0.2);
}
.search .nav .links {
  overflow: hidden;
}
.search .nav .links>li {
  float: left;
  margin-right: 25px;
}
.search .nav .links>li>a {
  display: block;
  font-size: 15px;
  height: 40px;
  line-height: 40px;
  color: #000;
}
.search .nav .links>li>a.active {
  color: #c62e2e;
  border-bottom: 2px solid #c62e2e;
}
.search .children {
  position: absolute;
  width: 95%;
  height: 380px;
  top: 160px;
  bottom: 0;
  overflow: auto;
}


