
.searchPlaylist {
  font-size: 13px;
}
.searchPlaylist .playlists {
  width: 100%;
}
.searchPlaylist .playlists .playlist {
  width: 100%;
  height: 70px;
  text-align: left;
  overflow: hidden;
}
.searchPlaylist .playlists .playlist>a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 70px;
  padding: 10px;
  box-sizing: border-box;
  cursor: default;
}
.searchPlaylist .playlists .playlist>a .cover {
  width: 50px;
  height: 50px;
  margin-right: 5px;
}
.searchPlaylist .playlists .playlist>a .name {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #5584b2;
}
.searchPlaylist .playlists .playlist>a .count {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.searchPlaylist .playlists .playlist>a .author {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.searchPlaylist .playlists .playlist>a .playCount {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.searchPlaylist .playlists .playlist:nth-child(odd) {
  background-color: #f4f4f6;
}


