
.wrapper {
  background-color: transparent;
  width: 100%;
  height: 60px;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99999;
}
.wrapper .play-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: 1px solid rgba(7,17,27,0.2);
}
.wrapper .play-wrapper .cover {
  width: 60px;
  height: 60px;
}
.wrapper .play-wrapper .cover>img {
  width: 100%;
  height: 100%;
}
.wrapper .play-wrapper .cover .mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  line-height: 60px;
  font-size: 25px;
  color: #fff;
  background-color: rgba(7,17,27,0.5);
  opacity: 0;
}
.wrapper .play-wrapper .cover:hover .mask {
  opacity: 1;
  transition: all 0.5s;
}
.wrapper .play-wrapper .detail {
  box-sizing: border-box;
  width: 100%;
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.wrapper .play-wrapper .detail .ply {
  width: 170px;
  padding-left: 15px;
  padding-top: 14px;
  overflow: hidden;
}
.wrapper .play-wrapper .detail .ply .preSongBtn,
.wrapper .play-wrapper .detail .ply .nextSongBtn,
.wrapper .play-wrapper .detail .ply .ply-btn {
  float: left;
  width: 32px;
  height: 32px;
  background-color: #df3b3b;
}
.wrapper .play-wrapper .detail .ply .preSongBtn>i,
.wrapper .play-wrapper .detail .ply .nextSongBtn>i,
.wrapper .play-wrapper .detail .ply .ply-btn>i {
