}
.wrapper .play-wrapper .detail .ply .nextSongBtn,
.wrapper .play-wrapper .detail .ply .ply-btn {
  margin-left: 20px;
}
.wrapper .play-wrapper .detail .controls {
  -webkit-box-flex: 4;
  -ms-flex: 4;
      flex: 4;
  box-flex: 4;
  min-width: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: left;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.wrapper .play-wrapper .detail .controls .top {
  height: 30px;
  line-height: 30px;
  font-size: 12px;
}
.wrapper .play-wrapper .detail .controls .top .bPlayingInfo {
  float: left;
}
.wrapper .play-wrapper .detail .controls .top .time {
  float: right;
  marging-right: 30px;
}
.wrapper .play-wrapper .detail .controls .bottom {
