}
.wrapper .play-wrapper .detail .controls .bottom .volLine {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 30px;
  cursor: pointer;
}
.wrapper .play-wrapper .detail .controls .bottom .volLine .noVol {
  width: 30px;
  height: 30px;
}
.wrapper .play-wrapper .detail .controls .bottom .volLine .vol {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
  height: 6px;
  margin-top: 10px;
  background-color: #e5e5e5;
