}
.wrapper .play-wrapper .detail .mode {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.wrapper .play-wrapper .detail .mode>span {
  display: block;
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  box-flex: 1;
  min-width: 0;
  font-size: 15px;
  color: #999;
  line-height: 60px;
  text-align: center;
  cursor: pointer;
}
.wrapper .songDetail {
  position: fixed;
  bottom: 60px;
  top: 30px;
  width: 100%;
  overflow: auto;
  background-color: #dddddc;
  text-align: left;
}
.wrapper .songDetail .bg-blur {
  width: 100%;
  height: 480px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  -webkit-filter: blur(100px);
  -moz-filter: blur(100px);
  -o-filter: blur(100px);
  -ms-filter: blur(100px);
  filter: blur(100px);
}
.wrapper .songDetail .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.wrapper .songDetail .content .top {
  height: 480px;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.wrapper .songDetail .content .top .left-cover {
  -webkit-box-flex: 1.5;
      -ms-flex: 1.5;
          flex: 1.5;
  height: 480px;
  position: relative;
}
.wrapper .songDetail .content .top .left-cover .cd-around {
  position: absolute;
  top: 40px;
  right: 90px;
  width: 310px;
  height: 310px;
  border: 5px solid rgba(7,17,27,0.4);
  border-radius: 50%;
  overflow: hidden;
  background-image: repeating-radial-gradient(#0b0b0b 0%, rgba(0,0,0,0.9) 3%);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
}
.wrapper .songDetail .content .top .left-cover .cd-around>img {
  width: 190px;
  height: 190px;
  margin: 0 0 0 0;
  border-radius: 50%;
}
.wrapper .songDetail .content .top .left-cover .cd-around.roll {
  -webkit-animation: roll 16s linear infinite;
          animation: roll 16s linear infinite;
}
.wrapper .songDetail .content .top .left-cover .cd-around.stop {
  -webkit-animation: roll 16s linear infinite;
          animation: roll 16s linear infinite;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.wrapper .songDetail .content .top .right-lyric {
  height: 480px;
  position: relative;
  -webkit-box-flex: 1.6;
      -ms-flex: 1.6;
          flex: 1.6;
}
.wrapper .songDetail .content .top .right-lyric>p {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #fff;
  font-size: 12px;
