  margin-bottom: 10px;
}
.wrapper .songDetail .content .top .right-lyric .info .album {
  margin-right: 25px;
}
.wrapper .songDetail .content .top .right-lyric .lyric {
  position: absolute;
  top: 100px;
  left: 20px;
  width: 100%;
  height: 300px;
  overflow: hidden;
}
.wrapper .songDetail .content .top .right-lyric .lyric .lines {
  position: absolute;
  top: 140px;
  left: 0;
}
.wrapper .songDetail .content .top .right-lyric .lyric .lines .line {
  height: 35px;
  line-height: 35px;
  font-size: 14px;
  letter-spacing: 0.1em;
}
.wrapper .songDetail .content .top .right-lyric .lyric .lines .line.line.active {
  color: #ffe358;
}
@-webkit-keyframes roll {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes roll {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}


