
.findMusic {
  padding: 20px 30px;
  font-size: 12px;
  text-align: left;
  position: relative;
}
.findMusic .children {
  position: absolute;
  width: 95%;
  height: 600px;
  top: 100px;
  bottom: 0;
  overflow: auto;
}
.nav {
  position: absolute;
  left: 30px;
  top: 40px;
  width: 90%;
  overflow: hidden;
  border-bottom: 1px solid rgba(7,17,27,0.2);
}
.nav .links {
  overflow: hidden;
}
.nav .links>li {
  float: left;
  margin-right: 25px;
}
.nav .links>li>a {
  display: block;
  font-size: 15px;
  height: 40px;
  line-height: 40px;
  color: #000;
}
.nav .links>li>a.active {
  border-bottom: 2px solid #c62e2e;
}


