
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 30px;
  bottom: 60px;
  width: 100%;
  overflow: auto;
}
.container #view {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
  color: #000;
  background-color: #fafafc; /*#fafafc;*/
}


