
.artistInfo {
  padding: 20px 30px;
  font-size: 12px;
  text-align: left;
}
.artistInfo .bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 180px;
  z-index: 1;
  -webkit-filter: blur(30px);
          filter: blur(30px);
  opacity: 0.2;
}
.artistInfo .info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
}
.artistInfo .info .avatar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 200px;
          flex: 0 0 200px;
  width: 200px;
  height: 200px;
  margin-right: 20px;
  position: relative;
}
.artistInfo .info .avatar>img {
  width: 100%;
  height: 100%;
}
.artistInfo .info .num {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-top: 4px;
  padding: 10px;
}
.artistInfo .info .num .name {
  margin-bottom: 45px;
  font-size: 18px;
}
.artistInfo .info .num>span {
  display: block;
  margin-bottom: 20px;
  font-size: 12px;
  letter-spacing: 1px;
}
.artistInfo .info .des {
  -webkit-box-flex: 8;
      -ms-flex: 8;
          flex: 8;
  padding-top: 40px;
  font-size: 14px;
  line-height: 25px;
}
.artistInfo .playAll {
  font-size: 15px;
  margin-left: 12px;
  margin-bottom: 15px;
  cursor: pointer;
}


